import React from 'react'


let homeMsg = {
	position:"relative",
	zIndex:"10",
	color: '#1890ff',
	textAlign: "center",
	fontSize: "14px",
	fontweight: "bold",
	width: "600px",
	margin: "150px auto",
};


class TableDemo extends React.Component {
	render() {
		return (
			<div >
			

				<div style={homeMsg}>
					<div style={{fontSize: "22px"}}>
                      FormDemo路由
					</div>
					<div>
                        山前梅鹿山后狼，狼鹿结拜在山岗； 
                        狼有难来鹿答救，鹿有难来狼躲藏； 
                        箭射乌鸦蓬头起，箭头落在狼身上； 
                        劝君交友需谨慎，千万莫交无意郎。
					</div>
					---------------------------------------------------------------------------------------------------------<br/>
					<div>
					  2019-11-29
					</div>
				</div>

			</div>
		)
	}
}

export default TableDemo