import React from 'react'
import Loading2 from '../Loading2/index'
import 'animate.css'
class Loading extends React.Component {
    constructor(prop){
        super(prop)
        const styles = {
            loadingBox:{
              position:'fixed',
              top:'50%',
              left:'50%',
              transform:'translate(-50%,-50%)'
            },
            loadingTitle:{
              position:'fixed',
              top:'50%',
              left:'50%',
              marginLeft: -45,
              marginTop: -18,
              color:'#000',
              fontWeight:500,
              fontSize:24
            },
            warp:{
                width: "100%",
                height: "100%",
                margin: 0,
                background:" #ddf3f3",
                padding: 0,
                overflow: "hidden",
            }
          }
          this.state ={
            styles:styles,
          }
          
    }
  
	render() {
		return (
            <div style={this.state.styles.warp}>
                <h3 style={this.state.styles.loadingTitle} className='animated bounceInLeft'>正在加载...</h3>
                <Loading2/>
            </div>
             
		)
	}
}

export default Loading