import React from 'react'


let homeMsg = {
	position:"relative",
	zIndex:"10",
	color: '#1890ff',
	textAlign: "center",
	fontSize: "14px",
	fontweight: "bold",
	width: "600px",
	margin: "150px auto",
};


class TableDemo extends React.Component {
	render() {
		return (
			<div >
			

				<div style={homeMsg}>
					<div style={{fontSize: "22px"}}>这里是基于react+ant.design的一套基本使用Demo。
					</div>
					<div>
						前端框架：reactJs
						UI框架：ant.design
						网络请求：axios
					</div>
					---------------------------------------------------------------------------------------------------------<br/>
					<div>
					   好事总得善人做，哪有凡人做神仙
					</div>
				</div>

			</div>
		)
	}
}

export default TableDemo