import { Layout, Menu, Icon } from 'antd';

import React, { Component } from 'react';
import 'antd/dist/antd.css';

import './nav.css'
import NavLink from '../NavLink'
import { Scrollbars } from 'react-custom-scrollbars';
const {  Sider } = Layout;
const { SubMenu } = Menu;

class SiderDemo extends Component {
  
      constructor(props) {
        super(props);
       
        const menus = [
          {
            id: "1",
            name: "首页",
            icon: "home",
            path: "/home"
          },
          {
            id: "2",
            name: "综合态势",
            icon: "radar-chart",
            path: "/formdemo"
          },
          {
            id: "3",
            name: "作业管理",
            icon: "cluster",
            children: [
              {
                id: "3-1",
                name: "作业监测",
                path: "/login"
              },
              {
                id: "3-2",
                name: "作业列表",
                path: "/test"
              },
              {
                id: "3-3",
                name: "集中控制",
                path: "/"
              }
            ]
          },
          {
            id: "4",
            name: "指令官管理",
            icon: "robot",
            children: [
              {
                id: "4-1",
                name: "任务列表",
                path: "/test"
              },
              {
                id: "4-2",
                name: "设备管理",
                path: "/"
              }
            ]
          },
          {
            id: "5",
            name: "权限管理",
            icon: "team",
            children: [
              {
                id: "5-0",
                name: "区域管理",
                path: "/test"
              },
              {
                id: "5-1",
                name: "用户管理",
                path: "/"
              },
              {
                id: "5-2",
                name: "角色管理",
                path: "/test"
              },
              {
                id: "5-3",
                name: "菜单管理",
                path: "/"
              }
            ]
          }
        ];
    
        const newMenu = menus.map(menu =>
          menu.children && menu.children.length > 0 ? (
            <SubMenu
              key={menu.id}
             
              title={
                <div>
                  <Icon type={menu.icon}  />
                  <span>{menu.name}</span>
                </div>
              }
            >
              {menu.children.map(children => (
                <Menu.Item key={children.id}>
                  <NavLink to={children.path}>{children.name}</NavLink>
                </Menu.Item>
              ))}
            </SubMenu>
          ) : (
            <Menu.Item key={menu.id}>
              <NavLink to={menu.path}>
                <Icon type={menu.icon}  />
                <span>{menu.name}</span>
              </NavLink>
            </Menu.Item>
          )
        );
        const scroll = {
            // 如果最终结果表格内容与表格头部无法对齐。
            // 表格头需要增加空白列，弹性布局
             width: '100%',
             // 最大高度，内容超出该高度会出现滚动条
             height: '100%',
             border:" 1px solid #ccc"
         }
        this.state = {
          collapsed: false,
          newMenus: newMenu,
          mode: 'inline',
          theme: 'Light',
          current: '1',
          scroll:scroll
          
        };

      }
       // 滚动结束，记下滚动位置
        handleScrollStop = () => {
            if (this.scrollbarsRef.current){
                this.scrollTop = this.scrollbarsRef.current.getScrollTop();
            }
        };

        componentDidMount() {
            //  覆盖ant design 自带滚动条样式
            // document.querySelector('.ant-table-scroll > .ant-table-body').style.overflow='hidden';
            // 滚动条组件ref，重新设置滚动位置
            this.scrollbarsRef = React.createRef();
        }

        // 组件重新渲染，重新设置滚动条的位置
        componentDidUpdate(prevProps, prevState, snapshot) {
            if (this.scrollbarsRef.current){
                this.scrollbarsRef.current.scrollTop(this.scrollTop);
            }
        }
       
    
      changeTheme = value => {
        this.setState({
          theme: value ? 'dark' : 'light',
        });
      };
    
      handleClick = e => {
        console.log('click ', e);
        this.setState({
          current: e.key,
        });
      };


    render() {
        return (
        
           
                <Sider
                    trigger={null}
                    collapsible
                    collapsed={this.props.collapsed}
                    width='200'
                   
                >
                     <Scrollbars style={this.state.scroll}
                        onScrollStop={this.handleScrollStop}
                        ref={ this.scrollbarsRef } >
                   <Menu
                    theme={this.state.theme}
                    defaultSelectedKeys={["1"]}
                    mode="inline"
                    className="sider-menu-container"
                 
                >
                    {this.state.newMenus}
                </Menu>
                </Scrollbars>
                </Sider>
               
            
        );
    }
}

export default SiderDemo;
