import React from 'react'
import Loading from '../../components/Loading/index'

import { Form, Icon, Input, Button, Checkbox } from 'antd';
class Login extends React.Component {
    constructor(prop){
        super(prop)
        const style ={
            loginBox:{
                width: "400px",
                height: "400px",
                position: "absolute",
                top: "50%",
                left: "50%",
                transform:" translate(-50%,-50%)",
                padding: "20px 50px",
           

            }
          
        }
        this.state ={
            loading:true,
            style
        }
          
    }
 
    componentDidMount () {
        setTimeout(()=>{
            this.setState({loading:false})
        },5000)
     };
     handleSubmit = (e) => {
        e.preventDefault();
       
        sessionStorage.setItem("isAuth", "hjb");
        this.props.history.push('/home')
	
	}
	render() {
        const {loading} = this.state
        const {loginBox} = this.state.style
        const { getFieldDecorator } = this.props.form;
		return (
            <div style={{height: "100%",width:"100%",    background:" #e6f7ff"} }>
           
            {
                loading?<Loading/> :
                <div style={loginBox}>
                    <h1 style={{textAlign: "center"}}>React-Hjb-Admin</h1>
                    <p style={{ textAlign: "center", color:" #999", marginbottom: "2em"}}>Belonging to Hao Jingbo React template</p>
                     <Form onSubmit={this.handleSubmit} className="login-form">
                        <Form.Item>
                        {getFieldDecorator('username', {
                            rules: [{ required: true, message: 'Please input your username!' }],
                        })(
                            <Input
                            prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            placeholder="Username"
                            />,
                        )}
                        </Form.Item>
                        <Form.Item>
                        {getFieldDecorator('password', {
                            rules: [{ required: true, message: 'Please input your Password!' }],
                        })(
                            <Input
                            prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                            type="password"
                            placeholder="Password"
                            />,
                        )}
                        </Form.Item>
                        <Form.Item>
                        {getFieldDecorator('remember', {
                            valuePropName: 'checked',
                            initialValue: true,
                        })(<Checkbox>Remember me</Checkbox>)}
                      
                      
                        </Form.Item>
                        <Form.Item>
                        <Button type="primary" htmlType="submit" style={{width:" 100%"}} className="login-form-button" onClick={this.handleSubmit.bind(this)}>
                           登录
                        </Button>
                        </Form.Item>
                    </Form>
                 
                </div>
            }

			</div>
		)
	}
}
const WrappedNormalLoginForm = Form.create()(Login);
export default WrappedNormalLoginForm