import Home from '../page/index'
import Login from '../page/Login'
import FromDemo from '../page/FromDemo/index'


const routes = [
    { path: '/home',
        exact: true,
		component: Home,
		requiresAuth: false,
    },
    {
        path: '/login',
        component: Login,
    },
    {
		path: '/formdemo',
		component: FromDemo,
		requiresAuth: false,//需要登陆后才能跳转的页面
    },
    // {
    //     path: '*',
    //     component: NotFound
    // }
]

export default routes