import React from 'react';

import SiderDemo from './nav/nav'
//全局文件
import './main';
import { BrowserRouter } from 'react-router-dom'
import ContentMain from './components/ContentMain'//主题
import './App.css'
import { Layout, Icon } from 'antd';
import 'antd/dist/antd.css';
import logo from './logo.svg';


const { Header, Content, Footer } = Layout;
let screenHeight= window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight;
class App extends React.Component {
  constructor(prop) {
    super(prop)
    this.state = {
      collapsed: false,

    };


  }
  toggle = () => {
    this.setState({
      collapsed: !this.state.collapsed,
    });
  }

  render() {
    return (
      <div className="App">
       
          <Header className="app-header">
            <span style={{ color: '#fff', paddingLeft: '2%', fontSize: '1.4em' }}>
              <Icon
                className="trigger"
                type={this.state.collapsed ? 'menu-unfold' : 'menu-fold'}
                onClick={this.toggle}
                style={{ cursor: 'pointer' }}
              />
            </span>
            <span style={{ color: '#fff', paddingLeft: '2%', fontSize: '1.4em',width: '400px' }}>Mrbabibo Management System</span>
            <span style={{ color: '#fff', float: 'right', paddingRight: '1%' }}>
              <img src={logo} className="App-logo" alt="logo" />
            </span>
          </Header>
          <BrowserRouter>
            <Layout>
        
              <SiderDemo collapsed={this.state.collapsed} />

              <Layout style={{ height: "100% " }}>
							<Content className="App-contentMain" style={{height:screenHeight}}>
								<ContentMain/>
							</Content>
                <Footer style={{ textAlign: 'center' }}>
                  Ant Design+react By Mrbabibo
                  
                  </Footer>
              </Layout>

            </Layout>
           
          </BrowserRouter>
       
        
      </div>


    );

  }
}
export default App;
